export default {
    name: 'Search',
    data () {
        return {
            searchText: '',
            tabCur: 'native',
            searchHistory: [],
            webTabs: [
                {
                    key: 'native',
                    value: '本站搜索'
                },
                {
                    key: 'ecdemic',
                    value: '全网搜索'
                }
            ],
            tipInfo: {
                'native': '搜索已录入在本站的书籍，搜索更快',
                'ecdemic': '海量全网搜索(自定义资源路径)'
            },
            sourceModal: false,
            sourceBox: [
                {
                    value: 'biquge',
                    name: '笔趣阁',
                    checked: false,
                    hot: false,
                },
                {
                    value: 'qidian',
                    name: '起点',
                    checked: false,
                    hot: false,
                },
                {
                    value: 'chuangshi',
                    name: '创世',
                    checked: false,
                    hot: false,
                },
                {
                    value: 'meiwen',
                    name: '美文',
                    checked: false,
                    hot: true,
                }
            ]
        }
    },
    onLoad() {
        this.searchHistory = uni.getStorageSync('searchHistory') || [];
    },
    methods: {
        tabSelect (e) {
            this.tabCur = e.currentTarget.dataset.web
        },
        inputBtn (e) {
            console.error(1, e.detail.value)
        },
        confirmBtn (e) {
            this.searchHistory.push(e.detail.value);
            uni.setStorageSync('searchHistory', this.searchHistory);
            console.error(2, e.detail.value)
        },
        clearBtn () {
            this.searchText = ''
        },
        hideSourceModal () {
            this.sourceModal = false
        },
        openSourceModal () {
            this.sourceModal = true
        },
        ChooseCheckbox(e) {
            let items = this.sourceBox;
            let current = e.currentTarget.dataset.value;
            for (let i = 0, lenI = items.length; i < lenI; ++i) {
                if (items[i].value === current) {
                    if (items[i].checked && this.sourceBox.filter(item => item.checked).length === 1) {
                        return;
                    }
                    items[i].checked = !items[i].checked;
                    break
                }
            }
        },
        deleteSearchHistoryBtn() {
            this.searchHistory = [];
            uni.removeStorageSync('searchHistory');
        },
    }
}
